import LocalStorageAdapter from './drivers/LocalStorageAdapter.js';
import { userContext } from '../core/UserContext.js';

/**
 * @class StorageRepository
 * The main entry point for data persistence.
 * Currently defaults to LocalStorageAdapter, but allows hot-swapping drivers.
 */
export class StorageRepository {
    constructor() {
        this.driver = new LocalStorageAdapter();
        this.initialized = false;
    }

    /**
     * Initializes the active storage driver.
     */
    async init() {
        if (this.initialized) return;

        // Default to LocalStorage
        await this.driver.init();
        console.log('[StorageRepository] Initialized with default driver:', this.driver.constructor.name);
        this.initialized = true;

        // Check implementation specific flags to see if we should upgrade to IndexedDB
        // We do this lazily or explicitly called by the bootstrapper
    }

    /**
     * Switches the underlying driver at runtime.
     * Useful after migration completes.
     * @param {StorageInterface} newDriver 
     */
    async switchDriver(newDriver) {
        console.log('[StorageRepository] Switching driver to:', newDriver.constructor.name);
        await newDriver.init();
        this.driver = newDriver;
    }

    // Use scoped keys for all operations
    // Note: MigrationService will need to be aware of this, or we expose a raw method.

    async getItem(key) {
        if (!this.initialized) await this.init();
        // Some keys might be global/system, but for the "Users/UUID" requirement, we default to scoped.
        const scopedKey = userContext.getScopedKey(key);
        return this.driver.getItem(scopedKey);
    }

    async setItem(key, value) {
        if (!this.initialized) await this.init();
        const scopedKey = userContext.getScopedKey(key);
        return this.driver.setItem(scopedKey, value);
    }

    async removeItem(key) {
        if (!this.initialized) await this.init();
        const scopedKey = userContext.getScopedKey(key);
        return this.driver.removeItem(scopedKey);
    }

    async clear() {
        // NOTE: clear() usually wipes the whole DB.
        // In a multi-user IDB, we should only remove keys starting with the current user prefix.
        // For now, assuming single-user-at-a-time in browser, clear() is likely intended as Factory Reset.
        // But to be proper:
        if (!this.initialized) await this.init();
        const keys = await this.driver.getKeys();
        const prefix = `user_${userContext.getUserId()}_`;
        const deletionPromises = keys
            .filter(k => k.startsWith(prefix))
            .map(k => this.driver.removeItem(k));
        await Promise.all(deletionPromises);
    }

    // --- High Level Domain Methods (To be expanded in Phase 2) ---

    /**
     * Loads a complete project by ID or Key
     * @param {string} projectKey 
     */
    async loadProject(projectKey) {
        return this.getItem(projectKey);
    }

    async getKeys() {
        if (!this.initialized) await this.init();
        // Return keys scoped to user, unscoped for the caller
        const allKeys = await this.driver.getKeys();
        const prefix = `user_${userContext.getUserId()}_`;
        return allKeys
            .filter(k => k.startsWith(prefix))
            .map(k => k.slice(prefix.length));
    }

    // Helper for projects (assuming they are stored with some distinction?)
    // If not, we iterate all and check metadata.
    // But `saveProject` stores them w/o prefix currently (based on my previous edit to storage.js).
    // Let's enforce a prefix 'project_' in `saveProject` effectively.

    /**
     * Saves a project
     * @param {string} projectKey 
     * @param {Object} projectData 
     */
    async saveProject(projectKey, projectData) {
        // Enforce prefix for better organization?
        // Legacy: projectKey IS the name.
        // If we change key structure, we break lookup by simple name.
        // But we can hide it in this repo.

        // Let's stick to simple key = name for now to maximize compatibility with the existing mess,
        // unless we want to clean it up now.
        // Cleaning up strongly recommended.

        // If we use simple key, 'settings' could be a project name.
        // Let's assume for this migration we keep as is, but `readAllProjects` will have to be smart.

        // Auto-backup
        // Asynchronous Auto-Backup (Fire & Forget) -> OPFS Data Vault
        // We import dynamically to avoid circular dependencies if any
        import('./DataVault.js').then(({ dataVault }) => {
            if (dataVault) {
                dataVault.saveSnapshot(projectKey, projectData).catch(err => console.warn('Auto-backup failed', err));
            }
        });

        return this.setItem(projectKey, projectData);
    }
}

// Export a singleton instance
export const storageRepo = new StorageRepository();
