/**
 * @class UserContext
 * Manages the current user identity (UUID).
 * Currently generates a random UUID if one doesn't exist.
 */
export class UserContext {
    constructor() {
        this.userId = null;
        this.storageKey = 'cine_user_uuid';
    }

    /**
     * Initializes the user context.
     * Checks localStorage for existing UUID, otherwise generates one.
     * We use localStorage for the UUID itself to maintain identity across IDB clears if needed,
     * or simpler: store it in the same storage as the data?
     * For independent "factory reset" safety, let's store it in localStorage for now as a "root" pointer.
     */
    init() {
        if (this.userId) return this.userId;

        // Try to get from localStorage (synchronous, legacy-safe)
        let uuid = localStorage.getItem(this.storageKey);

        if (!uuid) {
            if (typeof crypto !== 'undefined' && crypto.randomUUID) {
                uuid = crypto.randomUUID();
            } else {
                // Fallback for older environments
                uuid = 'user_' + Math.random().toString(36).substring(2, 15);
            }
            localStorage.setItem(this.storageKey, uuid);
            console.log('[UserContext] Generated new User UUID:', uuid);
        } else {
            console.log('[UserContext] Loaded User UUID:', uuid);
        }

        this.userId = uuid;
        return uuid;
    }

    getUserId() {
        if (!this.userId) return this.init();
        return this.userId;
    }

    /**
     * Scopes a storage key with the current user ID.
     * @param {string} key 
     */
    getScopedKey(key) {
        const uuid = this.getUserId();
        // Don't scope keys that are already system-level or shared if any?
        // For now, scope everything.
        return `user_${uuid}_${key}`;
    }

    /**
     * Clears the user identity (Factory Reset).
     */
    reset() {
        localStorage.removeItem(this.storageKey);
        this.userId = null;
    }
}

export const userContext = new UserContext();
